.pos_r{
    position: relative;
}

.pos_a{
    position: absolute;
}

.pull-left {
    float: left;
}

.pull-right{
    float: right;
}

i.icon{
    position: relative;
    transition: 1s;
}

i.icon::before{
    content: '';
    display: block;
    width: 50px;
    height: 50px;
    background-size: cover;
    background-repeat: no-repeat;
}

i.i-bus::before{
    background-image: url(../img/front-of-bus.svg);
}

i.i-ring::before{
    background-image: url(../img/wedding-rings.svg);
}

i.i-glasses::before{
    background-image: url(../img/cocktail.svg);
}

i.i-dishes::before{
    background-image: url(../img/dinner.svg);
}

i.i-music::before{
    background-image: url(../img/dance.svg);
}

.text{
    font-family: 'New Tegomin',serif;
    line-height: 150%;
    font-size: 16px;
    color: #8f8f8f;

    &_bold{
        font-weight: bold;
        font-family: 'New Tegomin',serif;
        line-height: 150%;
        font-size: 16px;
        color: #000;
    }
}

.top-header{
    min-height: 100vh;
    font-family: 'Pacifico', cursive;
    color: #fff;
    padding: 0 15px 30px;
    margin-bottom: 30px;

    &__background__blur{
        display: block;
        background-image: url(https://i.picsum.photos/id/1013/2600/1400.jpg?hmac=6sGSWGq_FODtSKwny_bvROgppve2-LrmwPhqFyDwUuA);
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        right: 0;
        left: 0;
        top: 0;
        bottom: 0;
        filter: blur(3px) brightness(0.6);
        z-index: 5;
    }

    &__container{
        top: 40%;
        left: 50%;
        transform: translateX(-50%);
        z-index: 6;
        text-align: center;
        width: 100%;
    }

    &__head{
        font-size: 60px;
        margin-bottom: 30px;
        line-height: 150%;
    }

    &__text{
        font-size: 36px;
        display: block;
        line-height: 150%;
    }

    @media (max-width: 640px) {
        &__container{
            padding: 0 15px;
            top: 30%;
        }

        &__head{
            font-size: 50px;
        }

        &__text{
            font-size: 30px;
        }
    }
}

.prehistory{
    display: table;
    padding: 80px 0;

    &__item {
        width: calc(100% / 2 - 30px);
        padding: 0 15px;
    }

    &__person-name{
        text-transform: uppercase;
        font-weight: 700;
        font-size: 36px;
        color: #000;
        font-family: 'New Tegomin', serif;
        line-height: 150%;
        display: block;
        margin-bottom: 15px;
    }

    &__text{
        font-size: 16px;
        font-family: 'New Tegomin', serif;
        color: rgb(143, 143, 143);
        line-height: 150%;
    }

    @media (max-width: 640px) {
        padding: 40px 0;

        &__item{
            width: 100%;
            margin-bottom: 30px;
        }

        &__person-name{
            font-size: 24px;
            margin-bottom: 10px;
        }
    }
}

.block-wed-det{
    padding: 80px 0px 220px;
    background: rgb(248, 243, 236);

    &__head{
        font-family: 'New Tegomin',serif;
        line-height: 150%;
        text-transform: uppercase;
        font-weight: 700;
        font-size: 36px;
        color: #000;
        display: block;
        margin-bottom: 20px;
        padding: 0 15px;
    }

    @media (max-width: 640px) {
        padding: 40px 0px;

        &__head{
            font-size: 24px;
            margin-bottom: 10;
        }
    }
}

.wed-det{

    &__item{
        width: calc(100% / 3 - 45px);
        min-width: 300px;
        margin: 0 auto;
        display: inline-block;
        padding: 10px 15px;

        &__head{
            font-family: 'New Tegomin',serif;
            line-height: 150%;
            text-transform: uppercase;
            font-weight: 700;
            font-size: 18px;
            color: #000;
            display: block;
            margin-bottom: 15px;
        }

        &__text{
            font-size: 16px;
            color: #8f8f8f;
            font-family: 'New Tegomin',serif;
            line-height: 150%;
        }
    }

    @media (max-width: 640px) {
        &__item {
            width: 100%;
            margin-bottom: 15px;

            &__head{
                margin-bottom: 10px;
            }
        }
    }
}

.block-icons{
    padding: 80px 40px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    position: relative;
    top: -133px;
    background: #fff;

    &__item{
        width: calc(100% / 5 - 15px);
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        transition: .4s ease-in;

        &:hover{
            transform: scale(1.2);
            transition: .4s ease;
        }

        &__icon{
            margin-bottom: 15px;
        }

        &__text{
            text-align: center;
        }

        @media (max-width: 640px) {
            width: calc(100% / 2 - 15px);
            margin-bottom: 30px;
        }
    }

    @media (max-width: 640px) {
        padding: 40px 10px;
        top: 0;

        &__item{
            width: calc(100% / 2 - 15px);
            margin-bottom: 30px;
        }
    }
}